import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniSection from '@/components/uni-section/uni-section.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import http from '@/common/request.js';
export default {
	components: {
		uniSection,
		uniList,
		uniListItem,
		uniNavBar
	},
	onShow() {
		// this.login();

		this.$store.dispatch('getUserInfo');

		this.getOffileFilesSize();
	},

	computed: {
		// 缓存文件大小
		badge() {
			// 如果是多少K
			if (this.total_size <= 1024 * 1024) {
				return parseInt(this.total_size / 1024) + 'K';
			}
			// 如果是多少M
			if (this.total_size > 1024 * 1024 && this.total_size <= 1024 * 1024 * 1024) {
				return parseInt(this.total_size / (1024 * 1024)) + 'M';
			}
			// 大于1个g都按照g算

			return parseInt(this.total_size / (1024 * 1024 * 1024)) + 'G';
		},

		// 是否已经登陆
		isLogin() {
			return this.$store.getters.getUserInfo && this.$store.getters.getUserInfo.UserID;
		},

		userLogo() {
			return this.$store.getters.getUserInfo.Logo;
		},
		RealName() {
			return this.$store.getters.getUserInfo.RealName;
		},
		UnitName() {
			return this.$store.getters.getUserInfo.UnitName;
		}
	},
	data() {
		return {
			current_head_cookie: '',
			input_head_cookie: '',
			username: 'stu_qiaokuoyuan',
			password: '123456',
			total_size: 0
		};
	},
	methods: {
		// 清除缓存文件
		clearOfflineFiles() {
			let that = this;
			console.log('clearOfflineFiles');
			uni.showModal({
				cancelText: '取消',
				confirmText: '确定',
				title: '提示',
				content: '确定要删除吗',
				success(r) {
					// 如过确定删除
					if (r.confirm) {
						uni.setStorageSync('download_task', '[]');

						// 先获取已经缓存的文件
						uni.getSavedFileList({
							success(r_fs) {
								let file_count = r_fs.fileList.length;
								let del_count = 0;

								let files = r_fs.fileList || [];

								for (let i = 0; i < files.length; ++i) {
									let delete_file_path= files[i].filePath
									uni.removeSavedFile({
										filePath: delete_file_path,
										success() {
											++del_count;
											console.log('已经删除' + del_count + '个', delete_file_path);
											if (del_count == file_count) {
												uni.showToast({
													title: '删除完成'
												});
											}
										}
									});
								}
							}
						});
					}
				}
			});
		},
		// 跳转到离线文件
		toMyOffline() {
			uni.navigateTo({
				url: '../offlineFiles/offlineFiles'
			});
		},

		// 获取离线缓存文件大小
		getOffileFilesSize() {
			let that = this;
			uni.getSavedFileList({
				success(r) {
					console.log('fs==>', JSON.stringify(r));
					that.total_size = 0;
					r.fileList
						.map(f => {
							return f.size;
						})
						.forEach(_size => {
							that.total_size += _size;
						});
				}
			});
		},

		// 登陆方法
		login() {
			let that = this;

			let _p = that.password;
			let _u = that.username;

			let sign = '';
			let timestamp = '';
			let encoded_passwrod = '';
			let token = '';
			let sid = '';
			let appid = '';

			// 先获取sign
			http.request({
				url: '/api/nologin/getsign',
				method: 'GET'
			}).then(r => {
				r = that.fr(r);
				if (r.Code == 200) {
					sign = r.Data.sign;
					timestamp = r.Data.timestamp;

					// 加密密码
					http.request({
						url: '/api/nologin/GetEncodePass?password=' + _p
					}).then(r_p => {
						r_p = that.fr(r_p);
						if (r_p.Code == 200) {
							encoded_passwrod = r_p.Data;

							// 根据加密后的密码 获取token

							uni.request({
								url: `https://ve.cnki.net/sso/m/login?appId=coeduapi&name=${_u}&pass=${encoded_passwrod}&code=local`,
								header: {
									appid: 'coeduapi',
									sign: sign,
									timestamp: timestamp
								}
							}).then(r3 => {
								r3 = that.fr(r3);

								if (r3.Head.ErrorCode == 0) {
									// 如过密码正确
									token = r3.Body.token;
									console.log('token is==>', token);
									uni.setStorageSync('token', token);
									uni.setStorageSync('sid', sid);
									uni.showToast({
										title: '登陆成功'
									});
									that.$store.dispatch('getUserInfo');
								} else {
									// 如过密码错误
									uni.showToast({
										title: r3.Head.ErrorMessage,
										icon: 'none'
									});
								}
							});
						}
					});
				}
			});
		},

		// 退出方法
		exit() {
			// 清空token和sid
			uni.setStorageSync('token', '');
			uni.setStorageSync('sid', '');

			// 清空用户信息  UserID
			let userInfo = this.$store.getters.getUserInfo;
			userInfo.UserID = 0;

			// 将每个参数都改成空

			// let userInfo2 = { ...userInfo };
			// let keys = Object.keys(userInfo2);

			// console.log('kyes:', keys);
			// keys.forEach(k => {
			// 	userInfo2[k] = '';
			// });

			console.log('new userInfo:', JSON.stringify(userInfo));
			this.$store.commit('setUserInfo', userInfo);
			console.log('new userInfo:', JSON.stringify(userInfo));
		}
	}
};
